import { Component, h, Prop } from '@stencil/core';
import { common } from "../../../global/data/common";
import { commonDynamic } from "../../../global/data/commonDynamic";
import { cart, CartData } from "../../../global/data/cart";
import * as cartFunctions from "../../../global/functions/cart";
import { DataLayer } from '../../../global/dataLayer';

@Component({
    tag: 'ks-page-cart',
    styleUrl: 'cart.css'
})
export class PageCart {
    @Prop() skipbase: boolean;
    @Prop() commonData: string;
    @Prop() commonDynamicData: string;

    @Prop() cartData: string;

    componentWillLoad() {
        const cartDataElement = document.getElementById(this.cartData);
		const cartData = JSON.parse(cartDataElement.innerHTML) as CartData;

        cartFunctions.update(cartData);
        DataLayer.view_cart(cartData);
    }

	render() {
        const commonStrings = common.get('translations');
        const strings = cart.get('strings');
        const discountStrings = cart.get('discountStrings');
        const easyprotectStrings = cart.get('easyprotectStrings');

        const progressBar = cart?.get('progressBar') && cart.get("productValue") < cart.get("shippingProgress")?.threshold;

		return <ks-page-base skipbase={this.skipbase} commonData={this.commonData} commonDynamicData={this.commonDynamicData}>
            
            { Object.keys(cart?.get('products')).length > 0 ? [

            <div class="cart-progress-container">
                { progressBar ? [
                    <ks-cart-progress-bar></ks-cart-progress-bar>,
                ] : null}
            </div>,
            
            <div class={"cart-container " + (!progressBar ? "no-top-padding" : "")}>
                <div class="cart-products">
                    <ks-cart-product-container></ks-cart-product-container>
                    <ks-cart-product-message></ks-cart-product-message>
                </div>

                <div class="cart-offers">
                    <ks-cart-deal-container></ks-cart-deal-container>

                    <ks-cart-recycle id="recycle">
                        <h2>{strings.recycleHeading}</h2>
                        <p>{strings.recycleParagraph}</p>
                    </ks-cart-recycle>

                    {easyprotectStrings ?
                        <ks-cart-easyprotect image={easyprotectStrings.image} width={easyprotectStrings.width} height={easyprotectStrings.height}>
                            <h2>{easyprotectStrings.heading}</h2>
                            <p>
                                {easyprotectStrings.description}
                                <a href={easyprotectStrings.pdf} target="_blank">{easyprotectStrings.seeMore}</a>
                            </p>
                        
                            <ks-cart-easyprotect-dialog slot="bottom">
                                <h2 slot="products">{easyprotectStrings.productsHeading}</h2>
                                <p slot="products">{easyprotectStrings.productsMessage}</p>

                                <h2 slot="warranty">{easyprotectStrings.warrantyHeading}</h2>
                                <p slot="warranty">{easyprotectStrings.warrantyMessage}</p>
                            </ks-cart-easyprotect-dialog>
                        </ks-cart-easyprotect>
                    : null}

                    <a href="/paypo-pm-71.html" class="paypo-message">
                        <ks-img2 src="/images/paypo-message.svg" width={287} height={72} alt="PayPo - Kupujesz teraz płacisz za 30 dni! Dostępne przez Przelewy24"/>
                    </a>
                </div>

                <div class="cart-summary">
                    <div class="cart-summary-sticky">
                        <h2>{strings.countrySelectHeading}</h2>
                        <ks-cart-country-select/>

                        <ks-cart-select-shipping name={strings.shippingName} />
                        <ks-cart-select-payment name={strings.paymentName} />

                        <ks-cart-discount-container
                            info-message={discountStrings.infoMessage}

                            code-banner={discountStrings.codeBanner}
                            code-placeholder={discountStrings.codePlaceholder}

                            points-placeholder={discountStrings.pointsPlaceholder}
                            points-message={discountStrings.pointsMessage}
                            login-message={discountStrings.pointsLoggedInMessage}
                            no-points-heading={discountStrings.noPointsHeading}
                            no-points-message={discountStrings.noPointsMessage}

                            threshold-heading={discountStrings.thresholdHeading}
                            threshold-message={discountStrings.thresholdMessage}

                            login-url={common.get('loginLink')}
                            logged-in={commonDynamic.get('loggedIn')}
                            disable-points={!cart.get('points')}
                        ></ks-cart-discount-container>

                        <ks-cart-summary-container></ks-cart-summary-container>

                        <ks-cart-buttons href={cart.get('api').order}></ks-cart-buttons>
                    </div>
                </div>
            </div>
        ] :
            <ks-nocontent
                link-name={commonStrings.noContentHome}
                back-name={commonStrings.noContentBack}
            >
                <h1>{cart.get('noContentHeading')}</h1>
                <p>{cart.get('noContentMessage')}</p>
            </ks-nocontent>
        }
        </ks-page-base>;
	}
}
