ks-page-cart .cart-progress-container {
    max-width: 1450px;
    padding: 20px 60px 0px 60px;
    box-sizing: border-box;
    width: 100%;
    margin: auto;
}

ks-page-cart .cart-container {
    display: grid; 
    grid-template-columns: 1fr 350px; 
    grid-template-rows: auto 1fr; 
    gap: 20px 20px; 
    grid-template-areas: 
        "cart-products cart-summary"
        "cart-offers cart-summary"; 
        
    justify-content: center;

    padding: 20px 60px;
    box-sizing: border-box;

    width: 100%;
    max-width: 1450px;
    margin: auto;
}

ks-page-cart .cart-products {
    grid-area: cart-products;
    max-width: 1000px;

    background-color: #ffffff;
    padding: 20px;
    box-sizing: border-box;
    border-radius: 10px;
}

ks-page-cart .cart-offers {
    display: flex;
    flex-direction: column;
    gap: 20px;

    grid-area: cart-offers;
    max-width: 1000px;
}

ks-page-cart .cart-summary {
    grid-area: cart-summary;

    position: relative;
}

ks-page-cart .cart-summary-sticky {
    position: sticky;
    top: 20px;

    background-color: #ffffff;
    padding: 20px;
    border-radius: 10px;
}

ks-page-cart h2 {
    font-weight: 700;
}

ks-page-cart ks-cart-easyprotect-product > .image {
    padding: 5px 0px;
    max-width: 70px;
    max-height: 70px;
}


ks-page-cart ks-cart-discount-code ks-input-text {
    margin-bottom: 0;
}

ks-page-cart ks-cart-discount-code .messsage {
    padding: 8px;
    box-sizing: border-box;
}

ks-page-cart .paypo-message {
    display: block;
    margin: 0 auto;
}


@media screen and (max-width: 1160px) {
    ks-page-cart .cart-container {
        display: block;
    }

    ks-page-cart .cart-offers {
        margin: 20px 0px;
    }

    ks-page-cart .cart-progress-container {
        padding: 20px 60px 0px 60px;
    }
}

@media screen and (max-width: 860px) {
    ks-page-cart .cart-container {
        padding: 20px 0px 0px 0px;
    }

    ks-page-cart .cart-container.no-top-padding {
        padding: 0px;
    }

    ks-page-cart .cart-progress-container {
        padding: 0px;
    }

    ks-page-cart .cart-progress-container > *,
    ks-page-cart .cart-products,
    ks-page-cart .cart-offers ks-cart-deal-container > *,
    ks-page-cart .cart-offers ks-cart-easyprotect,
    ks-page-cart .cart-summary > * {
        padding-right: 60px;
        padding-left: 60px;
    }

    ks-cart-country-select {
        margin-bottom: 10px;
    }
}

@media screen and (max-width: 640px) {
    ks-page-cart .cart-container {
        padding: 20px 0px 0px 0px;
    }

    ks-page-cart .cart-progress-container > *,
    ks-page-cart .cart-products,
    ks-page-cart .cart-offers ks-cart-deal-container > *,
    ks-page-cart .cart-offers ks-cart-easyprotect,
    ks-page-cart .cart-summary > * {
        padding-right: 20px;
        padding-left: 20px;
    }
}